body {
  font: normal normal 17px/1.235em "Arial", sans-serif; }

.nav-list {
  display: flex;
  margin: 0;
  padding: 0; }
  .nav-list__item {
    list-style: none;
    margin-right: 70px; }
    .nav-list__item:last-child {
      margin-right: 0; }
  .nav-list__link {
    display: block;
    position: relative;
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase; }
    .nav-list__link::after {
      content: "";
      position: absolute;
      top: 20px;
      left: 0;
      width: 16px;
      height: 2px;
      background: #ffffff;
      transition-duration: 0.2s; }
    .nav-list__link:hover::after {
      width: 100%; }

.header-nav__list {
  padding-right: 43px; }

.main-header {
  display: flex;
  justify-content: space-between;
  margin: 0; }

.main-wrapper {
  padding: 36px 7.1875% 0 7.1875%;
  background: url("../img/main-wrapper-bg.jpg") no-repeat; }

.socials {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0; }
  .socials__item {
    margin-right: 15px; }
  .socials__link {
    display: block;
    text-decoration: none;
    height: 25px; }

.banner {
  min-height: 800px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 30px; }
  .banner__logo {
    align-self: center; }
  .banner__link {
    text-decoration: none;
    position: relative;
    color: #fff; }
    .banner__link::after {
      content: "";
      position: absolute;
      top: 0;
      left: -22px;
      width: 19px;
      height: 19px;
      background: url("../img/call.png") no-repeat;
      z-index: 9; }

.gallery {
  font-size: 0;
  flex-direction: column;
  display: flex;
  flex-flow: row wrap;
  height: 200px; }
  .gallery__screen {
    flex: auto;
    height: auto; }

.table {
  background-color: #0F0F0F;
  min-height: 300px; }
  .table__text {
    display: block;
    text-align: center;
    color: #fff; }
  .table__text h2 {
    padding-top: 6rem;
    margin: 0px;
    text-transform: uppercase; }
  .table__text p {
    padding: 3.5rem;
    margin: 0px; }

.video {
  background: url("../img/video-screen.png") no-repeat;
  display: flex;
  min-height: 800px; }

.video-direction {
  display: flex;
  min-height: 600px;
  background-color: #0F0F0F;
  align-items: center;
  justify-content: center; }
  .video-direction__item {
    padding: 10px; }

.column-direction1 {
  display: flex;
  flex-direction: column;
  align-content: space-between; }

.column-direction2 {
  display: flex;
  flex-direction: column;
  align-content: space-between; }

footer {
  display: flex;
  background: url("../img/footer-bg.jpg") no-repeat;
  min-height: 200px;
  height: auto; }
